.nav-bar-brand {

  .snipcart-checkout {
    font-size: 12px;
    i {
      vertical-align: bottom;
    }
  }



  background-color: #470f6f;
  ul {
    list-style-type: none;
    margin: 0;
    padding: 0;
    overflow: hidden;

  }

  li {
    //float: left;
  }

  li a {
    display: block;
    color: white;
    text-align: center;
    padding: 14px 16px;
    text-decoration: none;
    background-color: #470f6f;
  }

  li a:hover:not(.active) {
    background-color: #5d1590;
  }

  .active {
    background-color: #7d22bd;
  }

  .nav-bar-container {
    width: 90%;

    max-width: 83.3em;
    margin: 0 auto;
  }
  .logo {
    font-family: 'Average', serif;
    font-size: 17px;
    padding: 12px 14px;
    &:hover {
      background-color: #470f6f !important;
    }
  }
}


