// ..........:::::::::: Usage ::::::::::.......... //

// <a class="flex-column">
//   Anything can be a button
// </a>

// ..........:::::::::: Flex ::::::::::.......... //

// Applied Flex Defaults:
//    flex-direction: row
//    justify-content: flex-start
//    align-items: flex-start
//    flex-wrap: nowrap

%flex {
  flex: 1 0 auto;
  display: flex; }

.flex {
  @extend %flex; }

// ..........:::::::::: Direction ::::::::::.......... //

// Default :::: row

.flex-column {
  @extend %flex;

  flex-direction: column; }


// ..........:::::::::: Justify Content ::::::::::.......... //

// Default :::: flex-start

.flex-space {
  @extend %flex;

  justify-content: space-between; }

.flex-center {
  @extend %flex;

  justify-content: center; }

.flex-end {
  @extend %flex;

  justify-content: flex-end; }

.flex-around {
  @extend %flex;

  justify-content: space-around; }


// ..........:::::::::: Align Items ::::::::::.......... //

// Default :::: flex-start

.flex-align-space {
  @extend %flex;

  align-items: space-between; }

.flex-align-center {
  @extend %flex;

  align-items: center; }

.flex-align-end {
  @extend %flex;

  align-items: flex-end; }


// ..........:::::::::: Wrap ::::::::::.......... //

// Default :::: nowrap

.flex-wrap {
  @extend %flex;

  flex-wrap: wrap; }


// ..........:::::::::: None ::::::::::.......... //

.flex-none {
  flex: none; }



