@import url("https://fonts.googleapis.com/css?family=Average|Lato:400,700,900");
@import "global";
@import "fonts";
@import "forms";
@import "loading-bar";
@import "flex";
@import "_3cols";



// overrides
.snip-layout .snip-btn--highlight {
  background-color: #470f6f;
  color: #fff;
  &:hover {
    background-color: #5d1590;
  }
}

.snip-layout .snip-header {
  font-family: 'Lato', sans-serif;
  color: #fff;
  font-weight: 400;
}

.snip-layout .snip-header {
  background: #58118a;
}

.snip-layout .snip-header__total {
  background: #4c1571;
  font-weight: 700;
}
.snip-layout .snip-header__total-label {
  color: #fff;
}

.snip-layout .snip-footer {
  background: #e6e6e6;
}

.snip-layout .snip-ico {
  color: #fff;
}
.snip-layout .snip-product__remove {
  color: #f10;
}
.snip-step__nb {
  color: #4c1571 !important;
}
.snipcart-summary {
  color: #dedede;
  //padding-right: 12px;
  font-size: 13px;
}
root {
  display: block;

  .loader {
    position: absolute;
    top: 40%;
    left: 45%;
    font-size: 24px;
    display: block;
    text-align: center;
    font-weight: 900;
  }
}



