input {
  font-size: 16px;
  padding: 10px 15px;
  border: 1px solid rgba(0, 0, 0, 0.1);
  border-radius: 3px;

  &:focus {
    border: 1px solid rgba(0, 133, 215, 1);
    outline: none;
  }
}
.btn-center {
  margin: 0 auto;
}
.btn {
  font-size: 14px;
  cursor: pointer;
}

.btn-full-width {
  width: 100%;
}



.btn-disabled {
  color: #fff;
  font-family: 'Lato', sans-serif;
  background-color: #ccc;
  border-radius: 4px;
  line-height: 20px;
  padding: 9px 20px;
  display: inline-block;
  text-decoration: none;
  -webkit-transition: all .5s;
  transition: all .5s;
  //margin: 30px 0;
  font-weight: normal;
  border: none !important;
}

.btn-disabled:active {
  background-color: #ccc;
}

.btn-disabled:visited {
  color: #fff;
}

.btn-primary {
  color: #fff !important;
  font-family: 'Lato', sans-serif;
  background-color: #470f6f;
  border-radius: 4px;
  line-height: 20px;
  padding: 9px 20px;
  display: inline-block;
  text-decoration: none;
  -webkit-transition: all .5s;
  transition: all .5s;
  //margin: 30px 0;
  text-decoration: none;
  font-weight: normal;
  border: none !important;
}
a.btn-primary {
  text-decoration: none;
}
.btn-primary[disabled] {
  background-color: #ccc;
  color: #ffffff;
  text-decoration: none;
}
.btn-primary[disabled]:hover {
  background-color: #ccc;
  color: #ffffff;
}

.btn-primary:active {
  background-color: #5B4D65;
}

.btn-primary:visited {
  color: #fff;
}

.btn-primary:hover {
  background-color: #7021A9;
  text-decoration: none;
}
.btn-green {
  background-color: #48a871;
  &:hover {
    background-color: #429160;
  }
}
.btn-light {
  background-color: #7d22bd;
}

.btn-secondary {
  color: #fff;
  font-family: 'Lato', sans-serif;
  border-radius: 4px;
  line-height: 20px;
  padding: 9px 20px;
  display: inline-block;
  text-decoration: none;
  -webkit-transition: all .5s;
  transition: all .5s;
  border: 2px solid #470f6f;
  color: #470f6f;
}

.btn-secondary:visited {
  color: #470f6f;
}

.btn-secondary:hover {
  background-color: #470f6f;
  text-decoration: none;
  color: #fff;
}


/* white button */

.btn-white {
  color: #470f6f;
  font-family: 'Lato', sans-serif;
  border-radius: 4px;
  line-height: 20px;
  padding: 9px 20px;
  display: inline-block;
  text-decoration: none;
  -webkit-transition: all .5s;
  transition: all .5s;
  margin: 20px 0;
  background-color: #DDD;
}

.btn-white:visited {
  color: #470f6f;
}

.btn-white:hover {
  background-color: #b250f8;
  color: #fff !important;
  text-decoration: none;
}


