.dynamic-input {
  border-color: transparent;

  &--no-contents {
    border-color: rgba(0, 0, 0, 0.1);
  }

  &:hover {
    border-color: rgba(0, 0, 0, 0.1);
  }

  &:focus {
    border-color: rgba(0, 133, 215, 1);
  }
}



