.auth {
  background: #fff;
  border: 1px solid rgba(0, 0, 0, .1);
  box-shadow: 0 3px 10px rgba(0, 0, 0, .1);
  padding: 20px 30px;
  width: 600px;
  margin: 50px auto 0;
  .auth-form {
    background: #fff;
  }
  hr {
    height: 1px;
    border-top: 1px solid #ccc;
    margin-bottom: 20px;
  }
  p {
    margin-bottom: 20px;
  }


  h1 {
    margin: 0 0 10px;
    background: #fff;
  }

  label {
    margin-bottom: 10px;
    display: block;
    background: #fff;
  }

  input {
    width: 100%;
    background: #fff;
  }

  &__info {
    text-align: center;
    margin-top: 20px;

    a {
      font-size: 12px;

      &:hover {
        text-decoration: underline;
      }
    }
  }

  .auth-button {
    text-align: center;
    background: #fff;
  }
}



