.membership-area {
  video {
    background: #000;
  }
  .module-list-container {
    width: 250px;
    margin-right: 30px;
  }
  a.bread-crumb {
    font-size: 1em;
    color: #470f6f;
  }
  .container {
    padding: 30px 40px;
  }

  .course-panel {
    background: #efefef;
    padding: 20px;
    border: 1px solid #e4e4e4;
    border-radius: 6px;
    margin-bottom: 30px;



    a.btn-primary {
      width: 22%;
      font-size: 16px;
      color: #fff;
      min-height: 65px;
    }
    &-left {
      padding-right: 45px;
      width: 80%;
    }

    &-title {
      font-weight: bold;
      font-size: 16px;
      color: #7b30b1;
      margin-bottom: 10px;
    }

    &-description {
      font-size: 13px;
      color: #565656;
      line-height: 19px;
    }
  }
