* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font: 400 14px/1.4 'Lato', 'Helvetica Neue', Helvetica, Arial, sans-serif;
  color: #5c5c5c;
  //background: #FFFFFF;
  background: #f1f1f1;
}
hr {
  border: 0;
  height: 0;
  border-top: 1px solid #e2e2e2;
  border-bottom: 1px solid #fff;
  margin-bottom: 20px;
}
a {
  color: #5c5c5c;
  text-decoration: none;
}

ul {
  list-style: none;
}

img {
  max-width: 100%;
}

form {
  //background: #fff;
}

footer {
  text-align: center;
  padding-bottom: 30px;
  font-size: 11px;
}

.site {
  display: flex;
  min-height: 100vh;
  flex-direction: column;
}

.site-content {
  flex: 1;
}


