.panel {
  box-shadow: 0 2px 5px 0 rgba(0, 0, 0, 0.1);
  background: #fff;
}
.panel-body {
  padding: 30px;
}

.section {
  margin-bottom: 30px;
}

.main-content {
  clear: both;
  width: 90%;
  font-size: 0.8125em;
  max-width: 100.3em;
  margin: 0 auto;
  padding: 1em 0px;
  color: #333;
  line-height: 1.5em;
  position: relative;
a {
  color: #47a6ef;
  text-decoration: underline;
  font-size: 14px;
  //margin-bottom: 18px;
  //display: inline-block;
}
  .page-header {
    font-weight: bold;
  }
  .sidebar-center {
    text-align: center;
  }
  .sidebar-header {
    border-bottom: 1px solid #e2e2e2;
    padding-bottom: 20px;
    margin-bottom: 20px;
  }
  .details-header {
    font-size: 2.5em;
    line-height: 40px;
    margin-bottom: 15px;
  }
  .list-header {
    margin-bottom: 10px;
    color: #525252;
  }
  .list-style {
    list-style-type: disc;
    margin-bottom: 20px;
    li {
      margin-left: 40px;
      margin-bottom: 10px;
      font-size: 14px;
      color: #6b6b6b;
    }
  }
  strong {
    font-weight: 700;
  }
  p {
    font-size: 0.880rem;
    margin-bottom: 15px;
    line-height:25px;
    color: #6b6b6b;
  }
  h3 {
    color: #525252;
    font-size: 16px;
    margin-bottom: 10px;
  }

  h4 {
    font-size: 15px;
    margin-bottom: 10px;
  }

  h1 {
    text-align: center;
    padding: 33px;
    font-weight: 400;
  }

  h2 {
    margin-bottom: 10px;
    font-size: 20px;
    line-height: 27px;
    letter-spacing: 0.3px;
    color: #525252;
  }

}


